.indexSlider {
    position: relative;
    min-height: 560px;
    background: $dark;
    text-align: center;
    .viewport {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 100%;
    }
    .slide {
        display: none;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        position: absolute;
        left: 0;
        top: 0;
        &:before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            @include gradient(-90deg, rgba(0,0,0,.6), rgba(0,0,0,0));
        }
        &:first-child {
            display: block;
        }
    }
    .controlsLayout {
        max-width: 1660px;
        margin: 0 auto;
        height: 0;
        position: relative;
    }
    .lister {
        display: inline-block;
        top: -50px;
        position: relative;
        z-index: 1;
        background: rgba(0,0,0,.35);
        padding: 8px 6px;
        font-size: 0;
        span {
            display: inline-block;
            vertical-align: middle;
            padding: 2px;
            border: 2px solid transparent;
            margin: 0 2px;
            &:before {
                content: '';
                display: block;
                width: 25px;
                height: 8px;
                background: $greyC7;
            }
            &:hover, &.current {
                border: 2px solid $greyC7;
            }
        }
    }
    .textLayout {
        position: relative;
        @include flex(flex-start,center,column);
        max-width: 1660px;
        margin: 0 auto;
        height: 100%;
        padding: 100px 20px 70px 20px;
    }
    .rightCenterText {
        max-width: 500px;
        font: 400 16px/24px $fira;
        color: $white;
        text-shadow: 0 0 7px rgba(0,0,0,.4);
        text-align: left;
        opacity: 0;
        transition: $transition;
        mark {
            font: 600 16px/24px $fira;
            @include gradient(180deg, $bluelight, $blue);
            padding: 0 12px;
            color: $white;
        }
    }
    .personBlock {
        color: $white;
        text-shadow: 0 0 7px rgba(0,0,0,.4);
        text-align: left;
        .img {
            display: none;
        }
        .text {
            max-width: 880px;
        }
        .citata {
            font: 400 16px/1.2 $fira;
            margin: 0 0 40px 50px;
            position: relative;
            opacity: 0;
            transform: translateY(500px);
            transition: $transition;
            &:before {
                content: '\00AB';
                display: block;
                width: 30px;
                font: 600 40px/25px $fira;
                position: absolute;
                left: -50px;
                top: -10px;
            }
        }
        .author {
            text-align: right;
            font: 400 14px/1.2 $fira;
            transform: translateY(500px);
            transition: $transition;
            .name {
                font: 600 16px/1.2 $fira;
                margin: 0 0 10px 0;
            }
        }
    }
    .animateSlide {
        .rightCenterText {
            opacity: 1;
        }
        .personBlock {
            .citata {
                opacity: 1;
                transform: translateY(0);
            }
            .author {
                opacity: 1;
                transform: translateY(0);
            }
        }
    }
    @include tablet {
        .controlsLayout {
            top: -50%;
        }
        .lister {
            width: 50px;
            position: absolute;
            left: 100%;
            top: 50%;
            padding: 6px 8px;
            margin: 0 0 0 -70px;
            span {
                display: block;
                margin: 2px 0;
                &:before {
                    width: auto;
                    height: 8px;
                }
            }
        }
        .textLayout {
            @include flex(flex-end,'','');
            padding: 120px 20px 100px 20px;
        }
        .rightCenterText {
            max-width: 600px;
            margin: 0 70px 0 0;
            font: 400 24px/36px $fira;
            mark {
                font: 600 24px/36px $fira;
            }
        }
        .animateSlide {
            .personBlock {
                .img {
                    opacity: 1;
                    img {
                        opacity: 1;
                        transform: translate(0,0);
                    }
                }
            }
        }
        .personBlock {
            @include flex(flex-end,'','','');
            //height: 100%;
            margin: 0 70px 0 0;
            .img {
                display: block;
                max-width: 350px;
                background: $bluemiddle;
                margin: 15px 15px 0 0;
                white-space: nowrap;
                opacity: 0;
                transition: $transition;
                &:before {
                    content: '';
                    display: inline-block;
                    vertical-align: top;
                    width: 0;
                    height: 0;
                    padding: 0 0 133% 0;
                }
                img {
                    height: 100%;
                    width: 100%;
                    margin: -15px -15px 15px 15px;
                    opacity: 0;
                    transform: translate(-30px,30px);
                    transition: $transition;
                    transition-delay: .3s;
                }
            }
            .text {
                margin: 0 20px 0 0;
            }
        }
    }
    @include laptop {
        min-height: 700px;
        .textLayout {
            padding: 140px 20px 100px 20px;
        }
        .rightCenterText {
            max-width: 620px;
            margin: 0 100px 0 0;
            font: 400 30px/45px $fira;
            mark {
                font: 600 30px/45px $fira;
            }
        }
        .personBlock {
            margin: 0 100px 0 0;
            .text {
                margin: 0 50px 0 33px;
            }
            .citata {
                font: 400 20px/1.3 $fira;
                margin: 0 0 40px 60px;
                &:before {
                    width: 40px;
                    font: 600 50px/30px $fira;
                    left: -60px;
                    top: -15px;
                }
            }
            .author {
                font: 400 16px/1.2 $fira;
                .name {
                    font: 600 20px/1.2 $fira;
                }
            }
        }
    }
    @include desktop {
        min-height: 800px;
        .lister {
            margin: 0 0 0 -90px;
        }
        .textLayout {
            padding: 175px 40px 100px 40px;
        }
        .rightCenterText {
            max-width: 780px;
            margin: 0 135px 0 0;
        }
        .personBlock {
            margin: 0 115px 0 0;
            .img {
                max-width: 400px;
            }
            .text {
                margin: 0 50px 0 100px;
                max-width: 880px;
            }
            .citata {
                font: 400 24px/1.4 $fira;
                margin: 0 0 50px 0;
                &:before {
                    width: 40px;
                    font: 600 60px/50px $fira;
                    left: -70px;
                    top: -20px;
                }
            }
            .author {
                font: 400 20px/1.2 $fira;
                .name {
                    font: 600 24px/1.2 $fira;
                    margin: 0 0 20px 0;
                }
            }
        }
    }
    @include desktopHD {
        min-height: 1000px;
        .textLayout {
            padding: 200px 20px 100px 20px;
        }
        .rightCenterText {
            max-width: 892px;
            font: 400 40px/60px $fira;
            mark {
                font: 600 40px/60px $fira;
            }
        }
        .personBlock {
            margin: 0 135px 0 0;
            .img {
                max-width: 430px;
            }
            .text {
                margin: 0 50px 0 155px;
            }
            .citata {
                font: 400 28px/1.4 $fira;
                &:before {
                    width: 50px;
                    font: 600 80px/50px $fira;
                    left: -90px;
                }
            }
            .author {
                .name {
                    font: 600 28px/1.2 $fira;
                }
            }
        }
    }
    @include fullSize {
        .rightCenterText {
            max-width: 990px;
        }
    }
}
.newsSection {
    @include gradient(135deg, $bluelight, $blue);
    padding: 0 0 25px 0;
    position: relative;
    &:before {
        content: '';
        width: 935px;
        height: 585px;
        position: absolute;
        left: 50%;
        bottom: 130px;
        margin: 0 0 0 -450px;
        background: url(../img/map.png) center center no-repeat;
        background-size: contain;
        opacity: .1;
    }
    .newsTabs {
        background: $white;
        margin: 0 -20px 20px -20px;
        padding: 20px 0 1px 0;
        position: relative;
        .tab {
            position: relative;
        }
        .tabNav {
            margin: 0 0 20px 0;
            text-align: center;
            position: relative;
            .tabLink {
                display: none;
                text-decoration: none;
                z-index: 1;

                &.active {
                    display: inline-block;
                    font: 500 30px/1.2 $fira;
                    border-bottom: 2px solid $bluemiddle;
                }
                &.blue {
                    color: $bluemiddle;
                    border-color: $bluemiddle;
                }
                &.dark {
                    color: $grey4;
                    border-color: $grey4;
                }
                &.grey {
                    color: $gray;
                    border-color: $gray;
                }
                &.red {
                    color: $red;
                    border-color: $red;
                }
            }
        }
    }
    .geoBlock {
        position: relative;
        border: 3px solid $white;
        color: $white;
        padding: 15px 20px 10px 20px;
        max-width: 600px;
        margin: 0 auto;
        .title {
            font: 500 21px/1.2 $fira;
            margin: 0 0 10px 0;
        }
        .link {
            font: 400 15px/1.2 $fira;
        }
        .map {
            display: block;
            text-decoration: none;
            margin: -25px -20px -10px -20px;
            will-change: transform;
            img {
                width: 100%;
                height: auto;
                margin-top: 60px;
                transform: translateZ(0);
                will-change: transform;
                transition: transform 1s ease;
                @include transform(scale(1.001));
            }
            &:hover {
                img {
                    @include transform(scale(1.08));
                }
            }
        }
        .geoSlider {
            position: relative;
            overflow: hidden;
            padding: 10px 0;
            max-width: 400px;
            margin: 30px auto 0 auto;
            .viewport {
                position: relative;
                overflow: hidden;
                height: 565px;
                margin: 0 0 24px 0;
            }
            .slide {
                width: 90%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                margin: 0 5%;
                //img {
                //    width: 100%;
                //}
                &:first-child {
                    display: block;
                }
            }
            .lister {
                text-align: center;
                span {
                    display: inline-block;
                    vertical-align: middle;
                    width: 16px;
                    height: 8px;
                    margin: 0 2px;
                    border: 1px solid $white;
                    cursor: pointer;
                    transition: $transition;
                    &:hover, &.active {
                        background: $white;
                    }
                }
            }
        }
        .pertnerLink {
            @include flex(center,center);
            background: $white;
            height: 170px;
            padding: 20px;
            box-shadow: 0 0 10px rgba(0,0,0,.2);
            margin-bottom: 25px;
            img {
                max-height: 150px;
            }
        }
    }
    @include tablet {
        padding: 30px 0;
        .newsTabs {
            background: $grayF9;
            margin: -95px 0 35px 0;
            padding: 30px 20px 15px 20px;
            .tabNav {
                margin: 0 0 30px 0;
                .tabLink {
                    &.active {
                        font: 500 27px/1.2 $fira;
                        text-transform: uppercase;
                    }
                }
            }
        }
        .geoBlock {
            @include flex(center,space-between);
            max-width: 100%;
            margin: 0;
            padding: 0 0 15px 0;
            .inner {
                padding: 20px 40px;
            }
            .title {
                font: 500 40px/1.0 $fira;
                margin: 0 0 15px 0;
            }
            .link {
                font: 400 20px/1.2 $fira;
            }
            .map {
                margin: -5px -30px 0px -30px;
            }
            .geoSlider {
                max-width: 50%;
                min-width: 350px;
                .viewport {
                    margin: 0 0 20px 0;
                }
                .lister {
                    span {
                        width: 31px;
                        height: 13px;
                        margin: 0 5px;
                    }
                }
            }
            .pertnerLink {
                height: 170px;
                padding: 20px;
                box-shadow: 0 0 20px rgba(0,0,0,.2);
            }
        }
        .newsList {
            .newsBox {
                margin: 0 0 15px 0;
            }
        }
    }
    @include laptop {
        padding: 45px 0;
        .newsTabs {
            margin: -115px 0 30px 0;
            padding: 30px 25px 15px 25px;
            .tabNav {
                @include flex(center,space-between);
                .tabLink {
                    font: 500 19px/1.4 $fira;
                    color: $graymiddle;
                    border-bottom: 1px solid $graymiddle;
                    display: inline-block;
                }
            }
        }
        .geoBlock {
            .geoSlider {
                min-width: 415px;
                max-width: 415px;
            }
        }
    }
    @include desktop {
        &:before {
            content: none;
        }
        .widthFix {
            @include flex();
        }
        .newsTabs {
            margin: -100px 30px 0 0;
            flex-grow: 1;
            padding: 30px 25px 15px 25px;
            &:before {
                content: '';
                width: 1870px;
                height: 1170px;
                position: absolute;
                left: -130px;
                bottom: 0;
                background: url(../img/map.png) center center no-repeat;
                background-size: contain;
                opacity: .1;
            }
            .tabNav {
                margin: 0 0 45px 0;
                .tabLink {
                    font: 500 16px/1.4 $fira;
                    &.active {
                        font: 500 20px/1.15 $fira;
                    }
                }
            }
        }
        .geoBlock {
            display: block;
            min-width: 33%;
            max-width: 33%;
            padding: 35px 0 15px 0;
            .title {
                font: 500 40px/1.0 $fira;
            }
            .geoSlider {
                min-width: 300px;
                max-width: 520px;
            }
        }
        .newsList {
            margin: 0;
        }
    }
    @include desktopHD {
        .newsTabs {
            .tabNav {
                margin: 0 0 43px 0;
                .tabLink {
                    font: 500 18px/1.4 $fira;
                    &.active {
                        font: 500 22px/1.15 $fira;
                    }
                }
            }
        }
        .geoBlock {
            min-width: 35%;
            max-width: 35%;
            .title {
                font: 500 40px/1.0 $fira;
            }
            .map {
                margin: -25px -30px 0 -30px;
            }
        }
    }
    @include fullSize {
        .newsTabs {
            margin: -115px 45px 0 0;
            padding: 30px 45px 15px 45px;
            .tabNav {
                .tabLink {
                    font: 500 19px/1.4 $fira;
                    &.active {
                        font: 500 25px/1.15 $fira;
                    }
                }
            }
        }
        .geoBlock {
            .map {
                margin: -45px -30px 20px -30px;
            }
        }
    }
}
.stripImg {
    padding: 25px 20px;
    a {
        display: block;
    }
    img {
        width: 100%;
