.photoGallery {
    position: relative;
    background: $black;
    color: $white;
    .widthFix {
        padding: 70px 20px;
    }
    .mainImg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        opacity: .9;
        transition: $transition;
        &:before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,.1);
        }
    }
    .text {
        position: relative;
        text-align: center;
        margin: 0 0 40px 0;
    }
    .title {
        font: 500 30px/30px $fira;
        text-shadow: 1px 1px 7px rgba(0,0,0,.4);
        margin: 0 0 10px 0;
        text-transform: uppercase;
    }
    .goTo {
        font: 500 18px/20px $fira;
        text-shadow: 1px 1px 7px rgba(0,0,0,.4);
        text-decoration: none;
        border-bottom: 2px solid $white;
        &:hover {
            border-bottom: 2px solid transparent;
        }
    }
    .gallerySlider {
        margin: 100px auto;
        max-width: 300px;
        position: relative;
        padding: 0 25px;
        .viewport {
            position: relative;
            overflow: hidden;
            height: 510px;
        }
        .slide {
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            padding: 10px 0;
            &.active {
                .galeryLink {
                    background: rgba(0, 0, 0, .85);
                    color: $blue;
                }
            }
        }
        .prevArr, .nextArr {
            width: 110px;
            height: 35px;
            position: absolute;
            left: 50%;
            margin: 0 0 0 -55px;
            cursor: pointer;
            &:before, &:after {
                content: '';
                display: block;
                width: 60%;
                height: 4px;
                position: absolute;
                background: $white;
            }
        }
        .prevArr {
            top: -45px;
            &:before {
                top: 0;
                left: -10%;
                transform: rotate(-30deg);
                transform-origin: 100% 0;
            }
            &:after {
                top: 0;
                right: -10%;
                transform: rotate(30deg);
                transform-origin: 0 0;
            }
        }
        .nextArr {
            bottom: -45px;
            &:before {
                bottom: 0;
                left: -10%;
                transform: rotate(30deg);
                transform-origin: 100% 100%;
            }
            &:after {
                bottom: 0;
                right: -10%;
                transform: rotate(-30deg);
                transform-origin: 0 100%;
            }
        }
        .galeryLink {
            background: rgba(0,0,0,.55);
            border: 2px solid $white;
            text-align: center;
            padding: 20px;
            height: 150px;
            @include flex('', center, column);
            font: 500 15px/1.4 $fira;
            text-decoration: none;
            box-shadow: 0 0 18px rgba(0,0,0,.2);
            transition: $transition;
            &:hover {
                background: rgba(0,0,0,.85);
                color: $blue;
            }
        }
    }
    @include tablet {
        .widthFix {
            @include flex(center,space-between,'');
        }
        .text {
            text-align: right;
        }
        .title {
            font: 500 48px/48px $fira;
            margin: 0 0 30px 0;
        }
        .goTo {
            font: 500 28px/36px $fira;
        }
        .gallerySlider {
            min-width: 300px;
            max-width: 300px;
            margin: 0;
            padding: 0;
            .galeryLink {
                font: 500 18px/1.4 $fira;
            }
        }
    }
    @include laptop {
        .widthFix {
            padding: 110px 20px;
        }
        .title {
            font: 500 70px/60px $fira;
            margin: 0 0 40px 0;
        }
        .goTo {
            font: 500 40px/40px $fira;
        }
        .gallerySlider {
            .viewport {
                height: 690px;
            }
            .slide {
                padding: 15px 0;
            }
            .galeryLink {
                height: 200px;
                font: 500 22px/1.4 $fira;
            }
        }
    }
    @include desktop {
        .widthFix {
            padding: 110px 40px;
        }
        .title {
            font: 500 80px/80px $fira;
        }
    }
    @include desktopHD {
        .widthFix {
            padding: 110px;
        }
        .title {
            font: 500 100px/90px $fira;
        }
    }
}
