        .loadLink {
            padding: 20px 20px 20px 5px;

            .docIco {
                min-width: 80px;
                max-width: 80px;
                margin: 0 20px 0 0;

                &:before {
                    left: 14px;
                    top: 45px;
                    font: 400 12px/22px $fira;
                }

                .icon {
                    @include iconSize(80px);
                }
            }

            .docName {
                font: 500 20px/1.4 $fira;
                margin: 0 0 10px 0;
            }

