                font: 500 17px/1.4 $fira;
            }
        }
    }
    @include desktopHD {
        .title {
            font: 500 30px/1.2 $fira;
        }
        .loadLink {
            .docName {
                font: 500 22px/1.4 $fira;
            }
        }
    }
}
