        .usefulInformation {
            .title {
                font: 500 26px/1.2 $fira;
                margin: 0 0 35px 0;
            }
            .infoList {
                font: 500 27px/1.2 $fira;
                padding: 40px 50px;
                height: 380px;
            }
        }
    }
    @include desktop {
        padding: 110px 20px 120px 20px;
        .usefulInformation {
            .infoList {
                font: 500 27px/1.2 $fira;
                padding: 40px 50px;
                height: 380px;
            }
        }
        .advertisingBlock {
            display: block;
            padding: 95px 20px 55px 20px;
            position: relative;
            &:before {
                content: '';
                display: block;
                width: 1000px;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                background: $grayD;
            }
            &:after {
                content: '';
                display: block;
                clear: both;
            }
            .advertisingSlider {
                width: 370px;
                float: right;
            }
        }
    }
    @include desktopHD {
        padding: 110px 40px 120px 40px;
        .downloadMaterials {
            margin: 0 30px;
        }
        .usefulInformation {
            margin: 0 30px;
            .title {
                font: 500 30px/1.2 $fira;
            }
            .infoList {
                height: 380px;
            }
        }
        .advertisingBlock {
            padding: 107px 30px 55px 50px;
            .advertisingSlider {
                width: 420px;
            }
        }
    }
}
.advertisingSlider {
    .viewport {
        position: relative;
        overflow: hidden;
        height: 337px;
        margin: 0 0 24px 0;
    }
    .slide {
        width: 100%;
        //height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        img {
            width: 100%;
        }
        &:first-child {
            display: block;
        }
    }
    .lister {
        text-align: center;
        position: relative;
        span {
            display: inline-block;
            vertical-align: middle;
            width: 15px;
            height: 15px;
            margin: 0 5px;
            background: $white;
            transition: $transition;
            &:hover, &.active {
                background: $bluemiddle;
            }
        }
    }
    @include desktopHD {
        .viewport {
            height: 380px;
            margin: 0 0 50px 0;
        }
    }
}