.contactCOS {
  max-width: 1103px;
  padding-bottom: 50px;

  @include tablet {
    padding-bottom: 127px;
  }

  &__wrapper {
    margin: 34px auto 0;
  }

  &__card {
    display: grid;
    grid-template-columns: minmax(60px, 100px) minmax(60%, 865px);
    grid-template-rows: minmax(50px, 100%) 1fr;
    gap: 0px 0px;
    margin-bottom: 28px;
    box-shadow: 0 1px 29px 1px rgba(0, 0, 0, 0.13);
    grid-template-areas:
      "button title"
      "staff staff";
    @include tablet {
      grid-template-columns: minmax(100px, 238px) minmax(60%, 865px);
    }
    @include smart {
      grid-template-areas:
        "button title"
        "logo staff";
    }

    &:first-child {
      margin-bottom: 36px;
    }
  }

  &__show-btn {
    grid-area: button;
    align-self: start;
    width: max-content;
    margin-left: 16px;
    margin-top: 12px;
    color: #0080ff;
    font: 400 13px/1.67 $fira;
    text-decoration: underline;
    text-align: start;
    border: none;
    background: transparent;
    cursor: pointer;

    @include smart {
      font-size: 15px;
    }
  }

  &__title {
    display: flex;
    align-items: center;
    font-size: 16px;
    padding: 7px 13px;
    grid-area: title;
    margin: 0;
    color: $white;
    text-transform: uppercase;
    text-align: start;
    letter-spacing: 0.1px;
    cursor: pointer;

    @include gradient(-60deg, #0080ff, $bluelight);
    @include tablet {
      padding: 12px 33px;
      font: 500 20px/1.25 $fira;
    }
  }

  &__logo {
    grid-area: logo;
    background-image: url("./../img/ulfire35x53.png");
    background-repeat: no-repeat;
    background-position: left 30px bottom 30px;
    background-size: 35px 53px;
  }

  &__info {
    grid-area: staff;
    padding: 0 0 20px 20px;
    position: relative;
    height: max-content;
    display: none;

    &_show {
      display: block;
    }

    @include smart {
      padding: 0 0 29px 33px;
    }

    &_block {
      display: flex;
      flex-direction: column;

      &:not(:first-child) {
        margin-top: 35px;

        @include smart {
          margin-top: 71px;
        }
      }

      & .contactCOS__staff:not(:first-of-type) {
        margin-top: 22px;
      }
    }

    & a:last-child {
      margin-bottom: 0;
    }
  }

  &__department {
    height: 100%;
    margin: 0 0 0 -20px;
    padding: 4px 10px;
    background-color: #ececec;
    font: 500 15px/1.47 $fira;
    font-style: italic;
    text-transform: uppercase;
    border-style: solid;
    border-width: 0px;
    border-left-width: 10px;
    border-image: linear-gradient(-50deg, #199af6 0%, #0080ff 100%);
    border-image-slice: 1;

    @include smart {
      font-size: 17px;
      margin: 0 0 0 -33px;
      padding: 4px 21px;
    }
  }

  &__staff {
    margin: 10px 0 0;
    color: #108feb;
    font: 500 20px/1.3 $roboto;

    @include smart {
      margin-top: 20px;
      font-size: 25px;
      line-height: 1.6;
    }
  }

  &__profession {
    margin-bottom: 12px;
    color: #4a4a4a;
    line-height: 25px;
  }

  &__contact {
    display: flex;
    flex-direction: column;
    margin-bottom: 7px;

    @include smart {
      flex-direction: row;
    }
    &_tel {
      font-size: 17px;
      text-decoration: none;
      width: fit-content;

      @include smart {
        font-size: 20px;
      }

      &::before {
        content: url("./../img/logo-tel.png");
        margin-right: 10px;

        @include smart {
          position: absolute;
          left: 0;
          top: auto;
          margin-right: 0;
        }
      }

      &_comma {
        display: none;

        @include smart {
          font-size: 20px;
          display: flex;
          align-items: flex-end;
        }
      }
    }

    &_email {
      font-size: 15px;
      text-decoration: none;
      width: fit-content;

      @include smart {
        font-size: 17px;
      }

      &::before {
        content: url("./../img/logo-email.png");
        margin-right: 10px;

        @include smart {
          position: absolute;
          left: 0;
          top: auto;
          margin-right: 0;
        }
      }
    }
  }
}
