* {margin: 0; padding: 0; outline: none; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-box-sizing: border-box;}
*:before, *:after {box-sizing: border-box; -moz-box-sizing: border-box; -webkit-box-sizing: border-box;}
html {height: 100%;}
body {
    height: 100%;
    min-height: 100%;
    font: normal 15px/1.5 $roboto;
    color: $dark;
}
aside, nav, footer, header, section, article, menu {display: block;}
iframe {border:none;}
a {color: inherit; cursor:pointer;}
a img {border:none;}
img {vertical-align: middle; max-width: 100%; height: auto;}
p { margin:0 0 20px 0;}
ol {margin: 0 0 20px 20px;}
ol li {margin: 0 0 10px 0;}
ul {margin: 0 0 20px 20px;}
ul li {margin: 0 0 10px 0;}
nav ul, nav ul li {list-style: none; margin: 0;}

#pageWrap {
    width: 100%;
    min-width: 320px;
    position: relative;
    overflow: hidden;
    background: $white;
    @include tablet {
        min-height: 100%;
        margin: 0 0 -500px 0;
    }
    @include desktop {
        margin: 0 0 -565px 0;
    }
}
#headerWrap {
    color: $white;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    padding: 15px 0 0 0;
    @include tablet {
        padding: 25px 0 0 0;
    }
    @include desktop {
        padding: 50px 0 0 0;
    }
}
#page {
    position: relative;
    @include tablet {
        min-height: 1000px;
        padding: 0 0 500px 0;
    }
    @include desktop {
        padding: 0 0 565px 0;
    }
}
#footerWrap {
    position: relative;
    @include gradient(-55deg, $blue, $blueblack);
    color: $white;
    @include tablet {
        height: 500px;
        margin: -500px 0 0 0;
    }
    @include desktop {
        height: 565px;
        margin: -565px 0 0 0;
    }
}
.openMenu {
    height: 100%;
    overflow: hidden;
}
.widthFix {
    max-width: 1660px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
    @include desktop {
        padding: 0 40px;
    }
}
.clearfix {
    &:after {
        content: '';
        display: block;
        width: 100%;
        height: 0;
        clear: both;
    }
}

.pageContent {
    padding: 35px 0 0 0;
}
.rightColumn {
    @include tablet {
        .borderBlock {
            width: 48%;
            float: left;
            &:nth-child(even) {
                float: right;
            }
        }
        &:after {
            content: '';
            display: block;
            clear: both;
        }
    }
    @include laptop {
        width: 30%;
        float: right;
        .borderBlock {
            width: auto;
            float: none!important;
        }
    }
}
.mainColumn {
    @include laptop {
        width: 70%;
        float: left;
        padding: 0 30px 0 0;
    }
    @include desktop {
        padding: 0 60px 0 0;
    }
}

.icon {
    display: block;
    @include iconSize(60px);
    fill: $white;
}

/* Titles */
h1, .H1 {
    font: 500 28px/1.0 $fira;
    margin: 0 0 15px 0;
    text-transform: uppercase;
    text-align: center;
    @include tablet {
        font: 500 50px/1.0 $fira;
        margin: 0 0 20px 0;
    }
    @include laptop {
        font: 500 60px/1.0 $fira;
        margin: 0 0 30px 0;
    }
    @include desktop {
        font: 500 70px/1.0 $fira;
    }
}
h2, .H2 {
    font: 500 20px/1.2 $fira;
    margin: 0 0 20px 0;
    text-align: center;
    @include tablet {
        font: 500 30px/1.0 $fira;
    }
    @include laptop {
        margin: 0 0 30px 0;
    }
    @include desktop {
        font: 500 40px/1.0 $fira;
    }
}
h3, .H3 {
    font: 500 18px/1.2 $fira;
    margin: 0 0 15px 0;
    @include tablet {
        font: 500 22px/1.0 $fira;
    }
    @include laptop {
        margin: 0 0 30px 0;
    }
    @include desktop {
        font: 500 30px/1.2 $fira;
    }
}
h4, .H4 {
    font: 500 16px/1.2 $fira;
    margin: 0 0 15px 0;
    @include tablet {
        font: 500 20px/1.0 $fira;
    }
    @include laptop {
        margin: 0 0 30px 0;
    }
    @include desktop {
        font: 500 25px/1.2 $fira;
    }
}

/*       */
.overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    background-color: rgba(0,0,0,0.5);
    display: none;
}
.popup-box {
    display: none;
    width: 300px;
    background-color: #e1e1e1;
    text-align: center;
    padding: 18px 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1000;
    visibility: visible;
    .close-btn {
        width: 10px;
        height: 0;
        padding-top: 9px;
        display: block;
        overflow: hidden;
        background: url('../img/close-btn.png') no-repeat 0 0;
        margin: 0 auto 18px;
    }
    input[type=text], input[type=password] {
        width: 240px;
        height: 40px;
        padding: 5px 10px;
        border: none;
        outline: none;
        background-color: #fff;
        font-family: 'Scada', sans-serif;
        font-size: 14px;
        line-height: 18px;
        color: #b0b0b0;
        font-weight: 400;
        text-transform: none;
        text-align: center;
        margin: 0 0 10px;
    }
    input[type=password] {
        margin: 0 0 20px;
    }
    button[type=submit] {
        width: 260px;
        height: 50px;
        background-color: #1280c4;
        border: none;
        outline: none;
        font-family: 'Scada', sans-serif;
        font-size: 14px;
        line-height: 18px;
        color: #fff;
        font-weight: 400;
        text-transform: uppercase;
        margin: 0 0 15px;
    }
    button[type=submit]:hover {
        background-color: #41b7ed;
    }
    ul {
        margin: 0;
        li {
            display: inline-block;
            font-family: 'Scada', sans-serif;
            font-size: 14px;
            line-height: 18px;
            color: #525252;
            font-weight: 400;
            a {
                font-family: 'Scada', sans-serif;
                font-size: 14px;
                line-height: 18px;
                color: #525252;
                font-weight: 400;
            }
        }
    }
}