.newsList {
    .newsBox {
        margin: 0 auto 20px auto;
        max-width: 600px;
        .newsLink {
            display: block;
            width: 100%;
            height: 100%;
            text-decoration: none;
            transition: $transition;
            &:hover {
                box-shadow: 0 0 20px rgba(0,0,0,.2);
                .imgBox {
                    .img {
                        &:before {
                            @include gradient(90deg, rgba(0,0,0,.5), rgba(0,0,0,.5));
                        }
                    }
                }
                .mainTxt {
                    .date, .label {
                        background: $bluemiddle;
                    }
                }
            }
        }
        .imgBox {
            overflow: hidden;
            position: relative;
            background: $blue;
            &:before {
                content: '';
                display: block;
                height: 0;
                padding: 75% 0 0 0;
            }
            .img {
                height: 100%;
                width: 100%;
                position: absolute;
                left: 0;
                top: 0;
                background-position: center center;
                background-repeat: no-repeat;
                background-size: cover;
                transition: $transition;
                &:before {
                    content: '';
                    height: 100%;
                    width: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                    @include gradient(90deg,rgba(0,0,0,.7),rgba(0,0,0,0));
                    transition: $transition;
                }
            }
        }
        .mainTxt {
            width: 100%;
            position: absolute;
            left: 0;
            bottom: 0;
            padding: 15px 20px;
            color: $white;
            .newsTitle {
                font: 400 18px/1.2 $fira;
            }
            .date, .label {
                display: inline-block;
                font: 400 13px/27px $roboto;
                padding: 0 15px 0 20px;
                margin: 0 0 10px -25px;
                transition: $transition;
            }
        }
        .anonsText {
            display: none;
        }
    }
    @include tablet {
        @include flex('',space-between,'',wrap);
        .newsBox {
            width: 46%;
            display: inline-flex;
            margin: 0 0 30px 0;
            .anonsText {
                display: block;
                padding: 20px;
                color: $gray;
                transition: $transition;
            }
            &:hover {
                .newsLink {
                    background: $white;
                }
                .anonsText {
                    color: $dark;
                }
            }
        }
    }
    @include laptop {
        .newsBox {
            margin: 0 0 45px 0;
            .mainTxt {
                padding: 15px 25px;
                .newsTitle {
                    font: 400 20px/1.2 $fira;
                }
                .date, .label {
                    margin: 0 0 10px -25px;
                    padding: 0 15px 0 25px;
                }
            }
            .anonsText {
                padding: 20px 25px;
            }
        }
    }
    @include desktop {
        margin: 0 0 45px 0;
    }
}
.subscribeForm {
    max-width: 400px;
    margin: 0 auto;
    h3 {
        color: $blue;
    }
    .note {
        color: $gray;
        font: 400 13px/1.4 $roboto;
    }
    .Button {
        margin: 0 0 20px 0;
    }
    .subscrInput {
        margin: 0 0 20px 0;
        box-shadow: 0 0 15px rgba(0,0,0,.2);
        @include flex();
        .mailIco {
            min-width: 45px;
            max-width: 45px;
            background: $blue;
            border-radius: 3px 0 0 3px;
            @include flex(center,center,'','');
            .icon {
                @include iconSize(28px);
            }
        }
        input {
            border: 2px solid $bluelight;
            border-radius: 0 3px 3px 0;
            flex-grow: 1;
            padding: 0 10px;
            font: 400 16px/40px $roboto;
        }
    }
    @include laptop {
        max-width: none;
        margin: 0;
    }
    @include desktop {
        .subscrInput {
            margin: 0 0 30px 0;
            .mailIco {
                min-width: 56px;
                max-width: 56px;
                .icon {
                    @include iconSize(38px);
                }
            }
            input {
                padding: 0 20px;
                font: 400 19px/53px $roboto;
            }
        }
    }
}
.newsPage {
    margin: 0 0 50px 0;
    h2 {
        text-align: left;
    }
    a {
        color: $bluemiddle;
    }
    .date, .label {
        display: inline-block;
        font: 400 17px/27px $roboto;
        padding: 0 15px;
        margin: 0 10px 10px 0;
        background: $bluemiddle;
        color: $white;
    }
    .labels {
        margin: 0 0 15px 0;
    }
    .mainImg {
        margin: 0 0 25px 0;
        img {
            max-width: 100%;
            height: auto;
            box-shadow: 0 0 20px rgba(0,0,0,.2);
        }
    }
    strong {
        color: $blue;
    }
    ul {
        list-style: none;
        margin: 0 0 25px 25px;
        li {
            position: relative;
            margin: 0 0 15px 0;
            &:before {
                content: url("data:image/svg+xml; utf8, <svg xmlns='http://www.w3.org/2000/svg' version='1.1' x='0' y='0' viewBox='0 0 11.26 17'><path fill='rgb(15,141,232)' d='M11.247,11.021c-0.258,3.522-2.98,6.193-6.08,5.966s-5.404-3.267-5.147-6.788C0.275,6.715,2.281,2.722,7.227,0C7.012,0.536,6.827,1.077,6.684,1.554C6.138,3.386,6.703,4.335,7.298,5.695C7.897,6.127,7.862,5.003,8.043,3.64C9.193,6.705,11.439,8.4,11.247,11.021z M5.239,10.098c-0.34,2.273-2.274,2.748-2.378,4.155c-0.102,1.407,0.93,2.63,2.307,2.73c1.376,0.101,2.576-0.958,2.678-2.364C7.951,13.21,7.3,11.486,5.239,10.098z'/></svg>");
                display: block;
                width: 12px;
                height: 17px;
                position: absolute;
                left: -22px;
                top: 3px;
            }
        }
    }
    .referenceBox, blockquote {
        margin: 0 0 25px 0;
        padding: 20px 15px;
        border-left: 6px solid $bluemiddle;
        background: $grayF6;
        p {
            margin: 0;
        }
        .title {
            font: 500 1em/1.4 $roboto;
            color: $bluemiddle;
            margin: 0 0 15px 0;
        }
    }
    @include smart {
        ul {
            margin: 0 0 25px 35px;
        }
        .referenceBox, blockquote {
            padding: 30px 25px;
            border-left: 9px solid $bluemiddle;
        }
    }
    @include desktop {
        font: 400 16px/1.6 $roboto;
    }
}