.searchForm {
    max-width: 400px;
    margin: 0 auto;
    h3 {
        color: $blue;
    }
    .input {
        margin: 0 0 25px 0;
    }
    @include laptop {
        max-width: none;
        margin: 0;
    }
}
.searchHead {
    margin: 0 0 25px 0;
    background: $white;
    box-shadow: 0 0 40px rgba(0,0,0,.1);
    padding: 7px;
    text-align: center;
    font: 500 20px/1.3 $fira;
    color: $graylight;
    .sectionName {
        display: block;
        padding: 10px 15px;
        @include gradient(-60deg, $blue, $bluelight);
        color: $white;
        box-shadow: 0 0 15px rgba(8,135,225,.2);
        margin: 0 0 10px 0;
    }
    .sectionLink {
        color: $bluemiddle;
    }
    @include smart {
        padding: 7px 18px 7px 7px;
        @include flex(center,space-between,'','');
        .sectionName {
            padding: 10px 25px;
            margin: 0 10px 0 0;
        }
    }
    @include tablet {
        font: 500 24px/1.3 $fira;
        .sectionName {
            padding: 10px 35px;
        }
    }
}
.searchList {
    margin: 0 0 50px 0;
    .searchBox {
        display: block;
        margin: 0 0 25px 0;
        text-decoration: none;
        font: 400 16px/1.5 $roboto;
        .iconBlock {
            @include gradient(150deg, $bluelight, $blue);
            color: $white;
            padding: 20px;
            .icon {
                margin: 0 auto;
                @include iconSize(60px);
            }
        }
        .description {
            padding: 15px;
        }
        .title {
            font: 500 18px/1.3 $fira;
            margin: 0 0 15px 0;
            text-decoration: underline;
            color: $blue;
            &:last-child {
                margin: 0;
            }
        }
        .searchDate {
            @include gradient(150deg, $bluelight, $blue);
            color: $white;
            @include flex(center,space-between,'','');
            padding: 15px;
            .date {
                font: 500 24px/.9 $fira;
            }
            .year {
                font: 500 16px/1.2 $fira;
            }
        }
        .plant {
            padding: 15px;
            .img {
                margin: 0 0 15px 0;
                text-align: center;
            }
        }
        .expert {
            margin: 0 0 25px 0;
            border-bottom: 2px solid $blue;
            overflow: hidden;
            .img {
                width: 110px;
                margin: 0 auto 15px auto;
            }
            .name {
                font: 500 18px/1.3 $fira;
                margin: 0 0 15px 0;
                text-decoration: underline;
                color: $blue;
            }
            .status {
                margin: 0 0 20px 0;
            }
        }
        &:hover {
            box-shadow: 0 0 45px rgba(0,0,0,.2);
            position: relative;
        }
    }
    @include smart {
        .searchBox {
            @include flex();
            .iconBlock {
                min-width: 100px;
                max-width: 100px;
            }
            .description {
                flex-grow: 1;
            }
            .title {
                margin: 0 0 20px 0;
            }
            .searchDate {
                @include flex(center,space-between,column,'');
                min-width: 100px;
                max-width: 100px;
                min-height: 150px;
                text-align: center;
                .date {
                    font: 500 30px/.9 $fira;
                }
                .year {
                    font: 500 20px/1.2 $fira;
                }
            }
            .plant {
                flex-grow: 1;
                .img {
                    text-align: left;
                }
            }
            .expert {
                .img {
                    float: right;
                    margin: 0 0 20px 20px;
                }
                .name {
                    margin: 0 0 20px 0;
                }
            }
        }
    }
    @include tablet {
        .searchBox {
            .iconBlock {
                @include gradient(150deg, $bluelight, $blue);
                color: $white;
                padding: 30px;
                min-width: 135px;
                max-width: 135px;
                .icon {
                    @include iconSize(75px);
                }
            }
            .description {
                padding: 25px;
            }
            .title {
                font: 500 22px/1.3 $fira;
            }
            .searchDate {
                padding: 25px;
                min-width: 135px;
                max-width: 135px;
                .date {
                    font: 500 36px/.9 $fira;
                }
                .year {
                    font: 500 20px/1.2 $fira;
                }
            }
            .plant {
                padding: 25px;
                @include flex(center,space-between,'','');
                .img {
                    max-width: 150px;
                    min-width: 150px;
                    margin: 0 0 0 20px;
                    order: 1;
                }
            }
            .expert {
                .name {
                    font: 500 22px/1.3 $fira;
                }
            }
        }
    }
    @include desktop {
        .searchBox {
            .plant {
                .img {
                    max-width: 180px;
                    min-width: 180px;
                }
            }
        }
    }
    @include desktopHD {
        .searchBox {
            .iconBlock {
                min-width: 160px;
                max-width: 160px;
            }
            .searchDate {
                min-width: 160px;
                max-width: 160px;
                .date {
                    font: 500 45px/.9 $fira;
                }
                .year {
                    font: 500 20px/1.2 $fira;
                }
            }
            .plant {
                .img {
                    max-width: 215px;
                    min-width: 215px;
                    margin: 0 0 0 40px;
                }
            }
        }
    }
}