.plantList {
    margin: 0 0 35px 0;
    h3 {
        text-align: left;
    }
    .plantBox {
        max-width: 480px;
        margin: 0 auto 30px auto;
        .inner {
            box-shadow: 0 0 45px rgba(0,0,0,.2);
            position: relative;
            overflow: hidden;
            &:hover {
                .moreLink {
                    opacity: 1;
                    transition-delay: .2s;
                }
                .bg {
                    transform: scaleY(2);
                }
            }
        }
        .img {
            height: 185px;
            line-height: 125px;
            text-align: center;
            background: $white;
            padding: 30px;
            img {
                max-height: 100%;
            }
        }
        .info {
            color: $white;
            flex-grow: 1;
            padding: 20px 20px 5px 20px;
            position: relative;
            > * {
                position: relative;
            }
        }
        .bg {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            bottom: 0;
            @include gradient(-60deg, $blue, $bluelight);
            transform: translateZ(0);
            transform-origin: 0 100%;
            transition: $transition;
        }
        .address {
            font: 500 14px/1.3 $roboto;
            margin: 0 0 25px 0;
        }
        .name {
            font: 500 22px/1.3 $roboto;
            text-transform: uppercase;
            margin: 0 0 10px 0;
        }
        .contacts {
            dt {
                font: 400 14px/1.3 $roboto;
                width: 80px;
                float: left;
                margin: 0 0 15px 0;
                color: $whiteblue;
            }
            dd {
                margin: 0 0 15px 85px;
                font: 500 15px/1.3 $roboto;
            }
        }
        .moreLink {
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 1;
            font: 500 30px/1.15 $roboto;
            text-transform: uppercase;
            display: block;
            padding: 20px;
            opacity: 0;
            transition: $transition;
            color: $white;
        }
    }
    @include tablet {
        margin: 0 -10px 30px -10px;
        @include flex('',flex-start,'',wrap);
        .plantBox {
            min-width: 50%;
            max-width: 50%;
            padding: 0 10px;
            display: inline-flex;
            display: -ms-inline-flexbox;
            margin: 0 0 30px 0;
            .inner {
                @include flex('','',column,'');
                width: 100%;
                height: 100%;
            }
        }
        h3 {
            padding: 0 10px;
            width: 100%;
        }
    }
    @include laptop {
        .plantBox {
            min-width: 33.3333%;
            max-width: 33.3333%;
            &.js-scrollAnimation {
                transform: translateY(300px);
                &:nth-child(3n+2) {
                    transition-delay: .2s;
                }
                &:nth-child(3n+3) {
                    transition-delay: .4s;
                }
                &.animationComplete {
                    transform: translateY(0);
                }
            }
        }
    }
    @include desktop {
        margin: 0 -30px 30px -30px;
        .plantBox {
            padding: 0 30px;
            margin: 0 0 45px 0;
            .moreLink {
                font: 500 40px/1.15 $roboto;
            }
        }
        h3 {
            padding: 0 30px;
        }
    }
    @include desktopHD {
        margin: 0 -40px 40px -40px;
        .plantBox {
            padding: 0 40px;
            margin: 0 0 45px 0;
        }
        h3 {
            padding: 0 40px;
        }
    }
}
.plantHead {
    box-shadow: 0 0 45px rgba(0,0,0,.2);
    margin: 0 -20px 30px -20px;
    .img {
        height: 150px;
        line-height: 110px;
        @include flex(center,center,'','');
        padding: 20px;
        img {
            max-height: 100%;
            width: auto;
        }
    }
    .info {
        @include gradient(-60deg, $blue, $bluelight);
        color: $white;
        padding: 20px 20px 10px 20px;
    }
    .name {
        font: 500 24px/1.2 $roboto;
        text-transform: uppercase;
        margin: 0 0 15px 0;
    }
    .contacts {
        dt {
            width: 80px;
            float: left;
            margin: 0 0 10px 0;
        }
        dd {
            margin: 0 0 10px 85px;
            font: 500 1em/1.5 $roboto;
        }
    }
    @include smart {
        margin: 0 0 30px 0;
    }
    @include tablet {
        @include flex();
        .img {
            min-width: 200px;
            max-width: 200px;
            height: auto;
        }
        .info {
            flex-grow: 1;
        }
    }
    @include laptop {
        margin: 0 0 60px 0;
        .img {
            min-width: 280px;
            max-width: 280px;
        }
        .info {
            padding: 40px 40px 30px 40px;
        }
        .name {
            font: 500 36px/1.2 $roboto;
            margin: 0 0 25px 0;
        }
        .contacts {
            @include flex('',space-between,'',wrap);
            dl {
                margin: 0 20px 0 0;
                min-width: 230px;
                max-width: 415px;
            }
            dt {
                font: 400 16px/1.4 $roboto;
                margin: 0 0 15px 0;
            }
            dd {
                margin: 0 0 15px 85px;
                font: 500 16px/1.4 $roboto;
            }
        }
    }
    @include desktop {
        margin: 0 0 60px 0;
        .img {
            min-width: 305px;
            max-width: 305px;
        }
        .contacts {
            flex-wrap: nowrap;
        }
    }
}
.callmeForm {
    max-width: 400px;
    margin: 0 auto;
    position: relative;
    h3 {
        color: $blue;
    }
    .input {
        margin: 0 0 25px 0;
    }
    .formMessage {
        @include flex(center,center,column,'');
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        font: 400 25px/1.3 $roboto;
        text-align: center;
        background: $white;
        border-radius: 5px;
        box-shadow: 0 0 15px rgba(0,0,0,.15);
        padding: 20px;
        .icon {
            fill: $blue;
            @include iconSize(80px);
            margin: 0 auto 40px auto;
        }
        .text {
            max-width: 300px;
            margin: 0 auto;
        }
    }
    @include laptop {
        max-width: none;
        margin: 0;
    }
}
.plantTabs {
    margin: 0 0 40px 0;
    .tabNav {
        background: $white;
        box-shadow: 0 0 40px rgba(0,0,0,.1);
        padding: 7px 4px 2px 4px;
        margin: 0 -20px 30px -20px;
        .tabLink {
            display: block;
            padding: 10px;
            font: 500 20px/1.3 $fira;
            color: $bluemiddle;
            flex-grow: 1;
            text-align: center;
            transition: $transition;
            margin: 0 3px 5px 3px;
            &.active, &:hover {
                @include gradient(-60deg, $blue, $bluelight);
                color: $white;
                text-decoration: none;
                box-shadow: 0 0 15px rgba(8,135,225,.2);
            }
        }
    }
    .downloadMaterials {
        .loadLink {
            margin: 0 0 10px 0;
        }
    }
    h2 {
        text-align: left;
    }
    .singleImg {
        margin: 0 0 30px 0;
    }
    @include smart {
        .tabNav {
            margin: 0 0 30px 0;
            @include flex(center, space-between, '', '');
            .tabLink {
                flex-grow: 1;
            }
        }
    }
    @include tablet {
        .downloadMaterials {
            @include flex('',space-between,'',wrap);
            margin: 0 -10px;
            .loadLink {
                flex-grow: 1;
                min-width: 46%;
                max-width: 46%;
                margin: 0 10px 10px 10px;
            }
        }
    }
    @include desktop {
        margin: 0 0 80px 0;
        .tabNav {
            padding: 7px 4px;
            margin: 0 0 40px 0;
            .tabLink {
                font: 500 24px/1.3 $fira;
                margin: 0 3px;
            }
        }
    }
}
.geographyTabs {
    margin: 0 0 40px 0;
    .tabNav {
        margin: 0 0 30px 0;
        position: relative;
        z-index: 1;
        .tabLink {
            display: inline-block;
            vertical-align: middle;
            font: 500 20px/1.3 $fira;
            color: $blue;
            transition: $transition;
            .icon {
                display: inline-block;
                vertical-align: middle;
                @include iconSize(30px);
                fill: $blue;
                margin: 0 10px 0 0;
                transition: $transition;
            }
            &:hover {
                color: $bluemiddle;
                text-decoration: none;
                .icon {
                    fill: $bluemiddle;
                }
            }
            &.active {
                display: none;
            }
        }
    }
    .listBox {
        @include columnsCount(4);
        column-width: 180px;
        ul {
            margin: 0 0 20px 100px;
            li {
                list-style: none;
                position: relative;
                page-break-inside: avoid;
                break-inside: avoid;
                a {
                    font: 400 12px/24px $roboto;
                    color: $graymiddle;
                    text-decoration: none;
                    &:hover{
                        color: $bluemiddle;
                    }
                }
                .letter{
                    position: absolute;
                    left: -30px;
                    top: 0;
                    font: 400 24px/24px $roboto;
                }
            }
        }
    }
}
#regionMap {
    position: relative;
    margin: -40px 0 0 0;
    svg {
        display: block;
        width: 100%;
        height: auto;
        .region {
            stroke: $white;
            stroke-width: 2;
            stroke-opacity: .25;
            fill: $bluemiddle;
            transition: $transition;
            cursor: pointer;
            &:hover {
                opacity: .5;
            }
        }
    }
    .mapLabel {
        position: absolute;
        left: 0;
        top: 0;
        border-radius: 3px;
        background: $dark;
        font: 500 15px/1.2 $roboto;
        padding: 4px 6px;
        color: $white;
        visibility: hidden;
        opacity: 0;
        transition: opacity .3s ease;
        &.visible {
            visibility: visible;
            opacity: 1;
        }
    }
    @include laptop {
        margin: -80px 0 0 0;
    }
}