.pageHead {
    min-height: 305px;
    @include gradient(-60deg, $bluedark, $bluelight);
    color: $white;
    text-align: center;
    padding: 170px 0 20px 0;
    position: relative;
    .bg {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: -4px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        &:before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,.5);
        }
    }
    .sectionNav {
        text-align: center;
        font: 500 14px/1.3 $fira;
        text-transform: uppercase;
        > div {
            display: inline-block;
            vertical-align: top;
            margin: 0 15px 10px 15px;
        }
        a {
            text-decoration: none;
            border-bottom: 1px solid $white;
            &:hover {
                border-bottom: 1px solid transparent;
            }
        }
    }
    @include tablet {
        height: 350px;
        padding: 200px 0 0 0;
        .bg {
            top: -6px;
        }
        .sectionNav {
            font: 500 18px/1.3 $fira;
        }
    }
    @include laptop {
        height: 430px;
        padding: 230px 0 0 0;
        .bg {
            top: -9px;
        }
    }
    @include desktop {
        height: 530px;
        padding: 300px 0 0 0;
    }
}
.centerText {
    max-width: 780px;
    margin: 0 auto 35px auto;
    text-align: center;
    font: 400 15px/1.5 $roboto;
    @include laptop {
        font: 400 20px/1.5 $roboto;
        margin: 0 auto 70px auto;
    }
}
.mainActivities {
    background: $grayF6;
    padding: 40px 0;
    text-align: center;
    position: relative;
    > * {
        position: relative;
    }
    .bgIco {
        @include iconSize(420px);
        position: absolute;
        left: 50%;
        bottom: -50px;
        margin: 0 0 0 -210px;
        fill: $white;
    }
    .activityList {
        max-width: 540px;
        margin: 0 auto 40px auto;
        .activityBox {
            margin: 0 0 15px 0;
            padding: 15px;
            text-align: left;
            position: relative;
            @include gradient(-60deg, $bluedark, $bluelight);
            color: $white;
            ul {
                list-style: none;
                margin: 0;
                position: relative;
                li {
                    margin: 0 0 25px 25px;
                    &:before {
                        content: '';
                        display: inline-block;
                        vertical-align: middle;
                        width: 15px;
                        height: 2px;
                        background: $white;
                        margin: 0 9px 0 -25px;
                    }
                    &:last-child {
                        margin: 0 0 0 25px;
                    }
                }
            }
            &.border {
                background: $white;
                border: 2px solid $bluelight;
                color: $dark;
                ul {
                    li {
                        &:before {
                            @include gradient(-60deg, $bluedark, $bluelight);
                        }
                    }
                }
            }
            &.image {
                background-position: center center;
                background-repeat: no-repeat;
                background-size: cover;
                &:before {
                    content: '';
                    display: block;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    left: 0;
                    top: 0;
                    background: rgba(0,0,0,.5);
                }
                ul {
                    li {
                        &:before {
                            background: $bluelight;
                        }
                    }
                }
            }
        }
    }
    @include laptop {
        padding: 40px 0 55px 0;
        h2 {
            margin: 0 0 50px 0;
        }
        .bgIco {
            @include iconSize(980px);
            bottom: -220px;
            margin: 0 0 0 -490px;
        }
        .activityList {
            max-width: none;
            margin: 0 -10px;
            @include flex('','','','');
            .activityBox {
                width: 33.3333%;
                flex-grow: 1;
                margin: 0 10px 45px 10px;
                transform: translateY(300px);
                &:nth-child(3n+2) {
                    transition-delay: .2s;
                }
                &:nth-child(3n+3) {
                    transition-delay: .4s;
                }
                &.animationComplete {
                    transform: translateY(0);
                }
            }
        }
    }
    @include desktop {
        .activityList {
            margin: 0 -40px;
            .activityBox {
                margin: 0 40px 65px 40px;
                padding: 25px;
                font: 400 17px/1.5 $roboto;
                ul {
                    li {
                        margin: 0 0 30px 40px;
                        &:before {
                            width: 28px;
                            height: 3px;
                            margin: 0 11px 0 -40px;
                        }
                        &:last-child {
                            margin: 0 0 0 40px;
                        }
                    }
                }
            }
        }
    }
}
.definitionList {
    max-width: 1100px;
    margin: 0 auto 30px auto;
    text-align: center;
    dl {
        dt {
            margin: 0 0 5px 0;
            color: $bluemiddle;
            font: 500 16px/1.4 $roboto;
        }
        dd {
            margin: 0 0 20px 0;
        }
    }
    @include tablet {
        margin: 0 auto 10px auto;
        font: 400 16px/1.4 $roboto;
        dl {
            dt {
                width: 40%;
                float: left;
                padding: 0 30px 20px 0;
                text-align: right;
                margin: 0;
            }
            dd {
                margin: 0 0 0 40%;
                padding: 0 0 20px 30px;
                text-align: left;
                &:before {
                    content: '\2013';
                    display: inline-block;
                    vertical-align: middle;
                    width: 12px;
                    text-align: center;
                    margin: 0 23px 0 -36px;
                }
                &:after {
                    content: '';
                    display: block;
                    clear: both;
                }
            }
        }
    }
    @include laptop {
        margin: 0 auto 40px auto;
        font: 400 17px/1.4 $roboto;
        dl {
            dt {
                width: 50%;
                padding: 0 30px 30px 0;
            }
            dd {
                margin: 0 0 0 50%;
                padding: 0 0 30px 30px;
            }
        }
    }
}
