            font-size: 0;
            margin: 0 -25px 0 0;
        }
        .personBox {
            display: inline-block;
            vertical-align: top;
            width: 50%;
            padding: 0 25px 25px 0;
            margin: 0;
            font: 400 14px/1.4 $roboto;
            .img {
