        }
        .personSet {
            margin: 0 0 0 -40px;
        }
        .personBox {
            padding: 0 0 55px 40px;
            font: 400 15px/1.4 $roboto;
            &:nth-child(2n) {
                transition-delay: .2s;
            }
            .img {
                width: 150px;
            }
