            text-align: left;
        }
        .personSet {
            margin: 0 0 0 20%;
        }
        .personBox {
            padding: 0 40px 55px 40px;
        }
    }
    @include desktopHD {
        .setTitle {
            width: 30%;
            font: 500 40px/1.0 $fira;
        }
        .personSet {
            margin: 0 0 0 30%;
        }
        .personBox {
            padding: 0 40px 55px 40px;
        }
    }
}
