            width: 135px;
        }
        .eventInfo {
            margin: 0 0 0 160px;
        }
        .eventDate {
            min-height: 220px;
            padding: 25px;
            .date {
                font: 500 36px/.9 $fira;
            }
        }
        .place {
            margin: 0 0 40px 0;
            dt {
                width: 150px;
                float: left;
            }
            dd {
                margin: 0 0 0 180px;
            }
        }
    }
    @include desktop {
        font: 400 16px/1.4 $roboto;
        h2 {
            margin: 0 150px 30px 0;
        }
        .img {
            margin: 0 0 30px 0;
        }
        .leftColumn {
            width: 155px;
            float: left;
        }
        .eventInfo {
            margin: 0 0 0 180px;
        }
        .eventDate {
            .date {
                font: 500 45px/.9 $fira;
                margin: 0 0 40px 0;
            }
        }
    }
}
.pageNote {
    max-width: 1100px;
    margin: 0 auto 30px auto;
    text-align: center;
    .note {
        margin: 0 0 30px 0;
    }
    .backSection {
        font: 500 18px/1.5 $fira;
        text-transform: uppercase;
        color: $bluemiddle;
    }
    @include laptop {
        font: 400 17px/1.5 $roboto;
    }
}
.pageArticle {
    max-width: 1080px;
    margin: 0 auto 65px auto;
    h1, h2, h3, h4 {
        text-align: left;
        text-transform: none;
    }
    h1 {
        font: 500 20px/1.3 $fira;
    }
    h2 {
        font: 500 18px/1.3 $fira;
        color: $blue;
    }
    h3 {
        font: 500 16px/1.3 $fira;
    }
    h4 {
        font: 500 14px/1.3 $fira;
    }
    .date {
        font: 500 1em/1.5 $roboto;
        color: $blue;
        margin: 0 0 20px 0;
    }
    .blueTitle {
        font: 500 16px/1.5 $fira;
        color: $blue;
        margin: 0 0 20px 25px;
    }
    .pageImg {
        margin: 0 0 25px 0;
        img {
            max-width: 100%;
            height: auto;
            box-shadow: 0 0 20px rgba(0,0,0,.2);
        }
    }
    ul {
        list-style: none;
        margin: 0 0 25px 25px;
        li {
            position: relative;
            margin: 0 0 15px 0;
            &:before {
                content: url("data:image/svg+xml; utf8, <svg xmlns='http://www.w3.org/2000/svg' version='1.1' x='0' y='0' viewBox='0 0 11.26 17'><path fill='rgb(15,141,232)' d='M11.247,11.021c-0.258,3.522-2.98,6.193-6.08,5.966s-5.404-3.267-5.147-6.788C0.275,6.715,2.281,2.722,7.227,0C7.012,0.536,6.827,1.077,6.684,1.554C6.138,3.386,6.703,4.335,7.298,5.695C7.897,6.127,7.862,5.003,8.043,3.64C9.193,6.705,11.439,8.4,11.247,11.021z M5.239,10.098c-0.34,2.273-2.274,2.748-2.378,4.155c-0.102,1.407,0.93,2.63,2.307,2.73c1.376,0.101,2.576-0.958,2.678-2.364C7.951,13.21,7.3,11.486,5.239,10.098z'/></svg>");
                display: block;
                width: 12px;
                height: 17px;
                position: absolute;
                left: -22px;
                top: 3px;
            }
        }
    }
    .referenceBox, blockquote {
        margin: 0 0 25px 0;
        padding: 20px 15px;
        border-left: 6px solid $bluemiddle;
        background: $grayF6;
        p {
            margin: 0;
        }
        .title {
            font: 500 1em/1.4 $roboto;
            color: $bluemiddle;
            margin: 0 0 15px 0;
        }
    }
    @include smart {
        ul {
            margin: 0 0 25px 35px;
        }
        .blueTitle {
            margin: 0 0 20px 35px;
        }
        .referenceBox, blockquote {
            padding: 30px 25px;
            border-left: 9px solid $bluemiddle;
        }
    }
    @include tablet {
        h1 {
            font: 500 30px/1.2 $fira;
        }
        h2 {
            font: 500 22px/1.2 $fira;
        }
        h3 {
            font: 500 20px/1.2 $fira;
        }
        h4 {
            font: 500 18px/1.2 $fira;
        }
    }
    @include desktop {
        font: 400 16px/1.6 $roboto;
        .blueTitle {
            font: 500 20px/1.5 $fira;
        }
        h1 {
            font: 500 40px/1.0 $fira;
        }
        h2 {
            font: 500 35px/1.0 $fira;
        }
        h3 {
            font: 500 30px/1.2 $fira;
        }
        h4 {
            font: 500 25px/1.2 $fira;
        }
    }
}
.faqList {
    margin: 0 0 40px 0;
    h3 {
        text-align: center;
    }
    .faqBox {
        margin: 0 0 30px 0;
        padding: 20px 15px;
        border-left: 6px solid $bluemiddle;
        background: $grayF6;
        .question {
            font: 500 1em/1.4 $roboto;
            color: $bluemiddle;
            margin: 0 0 15px 0;
        }
        .answer {
            padding: 0 0 0 20px;
            &:before {
                content: '\2014';
                display: inline-block;
                width: 10px;
                line-height: 2px;
                margin: 0 10px 0 -20px;
            }
        }
    }
    @include smart {
        .faqBox {
            padding: 30px 25px;
            border-left: 9px solid $bluemiddle;
        }
    }
    @include laptop {
        margin: 0 0 60px 0;
        .faqBox {
            margin: 0 0 45px 0;
        }
    }
}
.entryContacts {
    margin: 0 0 15px 0;
    text-align: center;
    .contactUnit {
        display: inline-block;
        vertical-align: top;
        margin: 0 15px 25px 15px;
        text-decoration: none;
        font: 400 20px/1.0 $roboto;
        .icon {
            display: inline-block;
            vertical-align: middle;
            margin: 0 10px 0 0;
            @include iconSize(24px);
            fill: $blue;
        }
    }
    @include laptop {
        margin: 0 0 40px 0;
        .contactUnit {
            margin: 0 20px;
            font: 400 24px/1.0 $roboto;
            .icon {
                @include iconSize(30px);
            }
        }
    }
    @include desktop {
        margin: 0 0 90px 0;
        .contactUnit {
            font: 400 28px/1.0 $roboto;
            .icon {
                margin: 0 15px 0 0;
                @include iconSize(37px);
            }
        }
    }
    @include desktopHD {
        .contactUnit {
            margin: 0 50px;
            font: 400 30px/1.0 $roboto;
            .icon {
                margin: 0 20px 0 0;
            }
        }
    }
}
.memberSlider {
    position: relative;
    overflow: hidden;
    padding: 20px 10px;
    @include gradient(135deg, $bluelight, $blue);
    margin: 0 0 30px 0;
    .title {
        font: 500 21px/1.2 $fira;
        margin: 0 10px 20px 10px;
        color: $white;
        text-transform: uppercase;
        text-align: center;
    }
    .viewport {
        max-width: 400px;
        margin: 0 auto 25px auto;
        position: relative;
        overflow: hidden;
        height: 565px;
    }
    .slide {
        width: 90%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        margin: 0 5%;
        &:first-child {
            display: block;
        }
    }
    .lister {
        text-align: center;
        span {
            display: inline-block;
            vertical-align: middle;
            width: 16px;
            height: 8px;
            margin: 0 2px;
            border: 1px solid $white;
            cursor: pointer;
            transition: $transition;
            &:hover, &.active {
                background: $white;
            }
        }
    }
    .pertnerLink {
        @include flex(center,center);
        background: $white;
        height: 170px;
        padding: 20px;
        box-shadow: 0 0 10px rgba(0,0,0,.2);
        margin-bottom: 25px;
        img {
            max-height: 150px;
        }
    }
    @include tablet {
        width: 48%;
        float: right;
        .viewport {
            margin: 0 0 20px 0;
            max-width: none;
        }
        .lister {
            span {
                width: 31px;
                height: 13px;
                margin: 0 5px;
            }
        }
    }
    @include laptop {
        width: auto;
        float: none;
    }
    @include desktop {
        margin: 0 0 50px 0;
    }
}
.mainContact {
    max-width: 1080px;
    margin: 0 auto 40px auto;
    @include gradient(-60deg, $blue, $bluelight);
    color: $white;
    box-shadow: 0 0 15px rgba(8,135,225,.2);
    font: 400 25px/1.4 $roboto;
    overflow: hidden;
    .address {
        @include flex(center,space-between,'','');
        padding: 25px 30px;
        border-bottom: 2px solid $white;
        margin: 0 0 25px 0;
        font: 400 30px/1.2 $roboto;
        .showMap {
            font: 500 17px/1.5 $roboto;
            margin: 0 0 0 15px;
            .icon {
                display: inline-block;
                vertical-align: middle;
                @include iconSize(28px);
                margin: 0 15px 0 0;
            }
        }
    }
    .unit {
        width: 50%;
        float: left;
        padding: 0 30px 15px 30px;
        > div {
            margin: 0 0 15px 0;
        }
    }
}
.contactsList {
    max-width: 1080px;
    margin: 0 auto 40px auto;
    .contactUnit {
        margin: 0 0 30px 0;
        .contactLabel {
            margin: 0 0 20px 0;
            font: 500 16px/1.5 $fira;
            text-align: center;
        }
        .contactInfo {
            margin: 0 -20px;
            background: $white;
            box-shadow: 0 0 30px rgba(0,0,0,.15);
            padding: 25px 30px 15px 30px;
            .infoLabel {
                display: inline-block;
                vertical-align: middle;
                font: 400 15px/23px $roboto;
                @include gradient(-60deg, $blue, $bluelight);
                color: $white;
                padding: 0 15px 0 30px;
                margin: 15px 0 15px -30px;
                &:first-child {
                    margin: 0 0 15px -30px;
                }
            }
            .contactName {
                font: 500 20px/1.5 $roboto;
                margin: 0 0 10px 0;
                color: $blue;
            }
            .contactStatus {
                margin: 0 0 10px 0;
                color: $grey4;
            }
            .contact {
                font: 400 17px/1.5 $roboto;
                margin: 0 0 10px 0;
                a {
                    text-decoration: none;
                }
                .icon {
                    display: inline-block;
                    vertical-align: middle;
                    fill: $blue;
                    @include iconSize(15px);
                    margin: 0 5px 0 -20px;
                }
            }
        }
    }
    @include tablet {
        max-width: 1080px;
        margin: 0 auto 100px auto;
        .contactUnit {
            @include flex(flex-start,center,'','');
            margin: 0 0 60px 0;
            .contactLabel {
                min-width: 50%;
                max-width: 50%;
                padding: 20px 30px 20px 0;
                text-align: right;
                margin: 0;
            }
            .contactInfo {
                min-width: 50%;
                max-width: 50%;
                margin: 0;
            }
        }
    }
    @include laptop {
        .contactUnit {
            .contactInfo {
                padding: 25px 30px 15px 50px;
                margin: 0;
                .infoLabel {
                    padding: 0 15px 0 50px;
                    margin: 15px 0 15px -50px;
                    &:first-child {
                        margin: 0 0 15px -50px;
                    }
                }
                .contactName {
                    font: 500 25px/1.5 $roboto;
                }
                .contact {
                    .icon {
                        margin: 0 15px 0 -30px;
                    }
                }
            }
        }
    }
}