.photoList {
    text-align: center;
    position: relative;
    font-size: 0;
    padding-top: 30px;
    .previewBox {
        margin: 0 0 30px 0;
        position: relative;
        &:before {
            content: '';
            display: block;
            padding: 0 0 67% 0;
        }
        .preview {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            overflow: hidden;
            border: 6px solid $white;
            box-shadow: 0 0 30px rgba(0,0,0,.23);
            border-radius: 3px;
            cursor: pointer;
            background-position: center top;
            background-repeat: no-repeat;
            background-size: cover;
            transition: $transition;
        }
    }
    &.openModal {
        .previewList {
            position: absolute;
            width: 100%;
        }
        .previewBox {
            .preview {
                transform: scale(0);
                opacity: 0;
            }
        }
    }
    @include tablet {
        .previewList {
            @include flex(flex-start, center, '', wrap);
            margin: 0 -10px;
        }
        .previewBox {
            flex-grow: 1;
            min-width: 40%;
            max-width: 47%;
            margin: 0 10px 30px 10px;
        }
    }
    @include laptop {
        .previewList {
            margin: 0 -15px;
        }
        .previewBox {
            min-width: 30%;
            max-width: 30%;
            margin: 0 15px 30px 15px;
        }
    }
    @include desktop {
        .previewBox {
            min-width: 20%;
            max-width: 22.5%;
        }
    }
    @include desktopHD {
        .previewList {
            margin: 0 -30px;
        }
        .previewBox {
            margin: 0 30px 60px 30px;
            max-width: 21%;
        }
    }
}
.modalView {
    display: none;
    position: relative;
    z-index: 100;
    padding: 0 0 40px 0;
    .imgBox {
        position: relative;
        .img {
            max-width: 100%;
            height: auto;
            max-height: 100%;
            border: 6px solid $white;
            box-shadow: 0 0 30px rgba(0,0,0,.23);
            border-radius: 3px;
        }
    }
    .closeLink {
        text-align: center;
        margin: -30px 0 40px 0;
        a {
            font: 500 18px/1.5 $fira;
            text-transform: uppercase;
            color: $bluemiddle;
        }
    }
    .modalPrev, .modalNext {
        display: none;
    }
    @include tablet {
        padding: 0 10px 40px 10px;
    }
    @include laptop {
        padding: 0 80px 80px 80px;
        .modalPrev, .modalNext {
            display: block;
            width: 160px;
            height: 100%;
            position: absolute;
            top: 0;
            font-size: 0;
            cursor: pointer;
            span {
                display: block;
                overflow: hidden;
                width: 40px;
                height: 105px;
                position: absolute;
                top: 50%;
                &:before, &:after {
                    content: '';
                    display: block;
                    width: 10px;
                    height: 70px;
                    position: absolute;
                    background: $blue;
                }
            }
        }
        .modalPrev {
            left: -120px;
            span {
                left: 60px;
                &:before {
                    top: -12px;
                    left: -1px;
                    transform: rotate(27deg);
                    transform-origin: 100% 100%;
                }
                &:after {
                    top: 50%;
                    left: 0;
                    transform: rotate(-27deg);
                    transform-origin: 0 0;
                }
            }
        }
        .modalNext {
            right: -120px;
            span {
                right: 60px;
                &:before {
                    top: -12px;
                    right: -1px;
                    transform: rotate(-27deg);
                    transform-origin: 0 100%;
                }
                &:after {
                    top: 50%;
                    right: 0;
                    transform: rotate(27deg);
                    transform-origin: 100% 0;
                }
            }
        }
    }
    @include desktopHD {
        padding: 0 120px 80px 120px;
    }
}
.gallerySection {
    .galleryBox {
        padding: 0 0 45px 0;
        text-align: center;
        .galleryLink {
            display: block;
            font: 400 19px/1.3 $roboto;
            color: $bluemiddle;
        }
        .galleryPreview {
            position: relative;
            max-width: 310px;
            margin: 0 auto 30px auto;
            &:before {
                content: '';
                display: block;
                padding: 68% 0 0 0;
            }
            .img {
                border: 6px solid $white;
                box-shadow: 0 0 30px rgba(0,0,0,.23);
                border-radius: 3px;
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                overflow: hidden;
                transform-origin: center bottom;
                transition: transform .5s ease, opacity .5s ease;
                img {
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
    @include tablet {
        margin: 20px -20px;
        font-size: 0;
        .galleryBox {
            display: inline-block;
            vertical-align: top;
            width: 50%;
            padding: 0 20px 50px 20px;
        }
    }
    @include laptop {
        margin: 50px -50px;
        .galleryBox {
            padding: 0 50px 75px 50px;
        }
    }
    @include desktop {
        margin: 70px -50px;
        .galleryBox {
            width: 33.3333%;
        }
    }
}