/* Forms */
textarea {resize:none; overflow: hidden;}
fieldset {border: none;}
.input {
    position: relative;
    input, textarea {
        color: $dark;
        display: block;
        background: $white;
        border-radius: 5px;
        border: 1px solid transparent;
        width: 100%;
        position: relative;
        box-shadow: 0 0 15px rgba(0,0,0,.15);
        &.error {
            border: 1px solid #ff9898;
            &::-webkit-input-placeholder {color: #ff1111;}
            &::-moz-placeholder {color: #ff1111;}
            &:-moz-placeholder {color: #ff1111;}
            &:-ms-input-placeholder {color: #ff1111;}
        }
    }
    input {
        font: 400 19px/43px $roboto;
        padding: 0 25px;
    }
    textarea {
        padding: 25px;
        font: 400 19px/1.2 $roboto;
        height: 115px;
    }
    @include laptop {
        input {
            font: 400 19px/57px $roboto;
        }
    }
}
.Button {
    display: inline-block;
    vertical-align: middle;
    text-decoration: none;
    text-align: center;
    white-space: nowrap;
    transition: $transition;
    position: relative;
    border-radius: 5px;
    font: normal 14px/30px $roboto;
    cursor: pointer;
    overflow: hidden;
    input {
        display: block;
        width: 100%;
        background: none;
        border: none;
        color: inherit;
        font: inherit;
        text-transform: inherit;
        text-align: inherit;
        padding: 0 40px;
        cursor: pointer;
        &:disabled {
            cursor: default;
        }
    }
    &.gradientBtn {
        display: block;
        @include gradient(-60deg, $blue, $bluelight);
        color: $white;
        box-shadow: 0 0 15px rgba(0, 0, 0, .2);
        text-transform: uppercase;
        font: 500 20px/38px $fira;
        padding: 0 15px;
        &:hover {
            transform: scale(1.05);
        }
        @include tablet {
            display: inline-block;
            font: 500 28px/50px $fira;
            padding: 0 30px;
        }
        @include laptop {
            font: 500 40px/70px $fira;
            padding: 0 50px;
        }
        @include desktop {
            font: 500 50px/80px $fira;
            padding: 0 90px;
        }
    }
    &.smallGradientBtn {
        display: block;
        @include gradient(-60deg, $blue, $bluelight);
        color: $white;
        box-shadow: 0 0 15px rgba(0,0,0,.2);
        font: 500 18px/38px $fira;
        input {
            padding: 0 15px;
        }
        &:hover {
            transform: scale(1.05);
        }
        @include tablet {
            font: 500 20px/42px $fira;
        }
        @include desktop {
            font: 500 25px/50px $fira;
        }
        @include desktopHD {
            font: 500 28px/57px $fira;
        }
        @include fullSize {
            font: 500 30px/57px $fira;
        }
    }
}
a.Button, button.Button {
    padding: 0 40px;
}
.singleIco {
    text-align: center;
    margin: 0 0 20px 0;
    .icon {
        display: inline-block;
        vertical-align: middle;
        @include iconSize(28px);
        fill: $blue;
    }
}
.loadLink {
    @include flex(center,'',row);
    text-decoration: none;
    transition: $transition;
    .docIco {
        min-width: 40px;
        max-width: 40px;
        position: relative;
        margin: 0 10px 0 0;
        &:before {
            content: attr(data-extension);
            position: absolute;
            left: 5px;
            top: 24px;
            font: 400 8px/10px $fira;
            color: $blue;
            text-transform: uppercase;
            transition: $transition;
        }
        .icon {
            @include iconSize(40px);
        }
    }
}
.pagerBox {
    margin: 0 0 40px 0;
    text-align: center;
    .pageLink {
        display: inline-block;
        vertical-align: middle;
        text-decoration: none;
        margin: 0 10px 10px 10px;
        width: 46px;
        font: 400 20px/44px $roboto;
        border: 1px solid $grayF6;
        color: $graylight;
        transition: color .3s ease;
        &.current, &:hover {
            border: 2px solid $blue;
            color: $bluemiddle;
            font: 400 20px/42px $roboto;
        }
    }
    @include desktop {
        .pageLink {
            width: 66px;
            font: 400 30px/64px $roboto;
            &.current, &:hover {
                font: 400 30px/62px $roboto;
            }
        }
    }
}
.backLink {
    display: inline-block;
    vertical-align: middle;
    margin: 0 0 25px 0;
    color: $graylight;
}
.videoLink {
    @include flex(center,'','','');
    color: $bluemiddle;
    font: 500 16px/1.3 $roboto;
    margin: 0 0 20px 0;
    .icon {
        fill: $bluemiddle;
        @include iconSize(25px);
        margin: 0 10px 0 0;
    }
}
.js-scrollAnimation {
    position: relative;
    transition: $transition;
    opacity: 0;
    &.animationComplete {
        opacity: 1;
    }
}
.borderBlock {
    border: 2px solid $bluelight;
    padding: 10px;
    position: relative;
    margin: 0 0 30px 0;
    .calendarIco {
        position: absolute;
        right: 15px;
        top: 10px;
        .icon {
            @include iconSize(25px);
            stroke: $bluemiddle;
        }
        & + h3 {
            margin-right: 30px;
        }
    }
    &.bannerBox {
        @include flex(center,center,'','');
    }
    @include laptop {
        padding: 20px;
        .calendarIco {
            right: 20px;
            top: 20px;
            .icon {
                @include iconSize(35px);
            }
            & + h3 {
                margin-right: 40px;
            }
        }
    }
    @include desktop {
        margin: 0 0 50px 0;
        padding: 20px 25px;
        .calendarIco {
            right: 25px;
        }
    }
    @include desktopHD {
        padding: 30px 45px;
        .calendarIco {
            right: 45px;
            top: 30px;
        }
    }
}
.advBlock {
    display: block;
    max-width: 400px;
    margin: 0 auto;
    img {
        width: 100%;
        height: auto;
    }
    @include laptop {
        max-width: none;
        margin: 0;
    }
}
.datepicker {
    max-width: 400px;
    margin: 0 auto;
    @include laptop {
        //max-width: none;
        //margin: 0;
    }
}
.ui-datepicker {
    width: 100%;
    text-align: center;
    .ui-datepicker-header {
        @include gradient(180deg, $bluelight, $blue);
        border-radius: 3px;
        color: $white;
        padding: 10px;
        margin: 0 0 15px 0;
        box-shadow: 0 0 15px rgba(0,0,0,.2);
        .ui-datepicker-prev, .ui-datepicker-next {
            font-size: 0;
            width: 20px;
            height: 30px;
            position: relative;
            cursor: pointer;
            display: block;
            overflow: hidden;
            &:before, &:after {
                content: '';
                display: block;
                width: 6px;
                height: 22px;
                position: absolute;
                background: $white;
            }
        }
        .ui-datepicker-prev {
            float: left;
            &:before {
                top: -5px;
                left: 0;
                transform: rotate(30deg);
                transform-origin: 100% 100%;
            }
            &:after {
                top: 50%;
                left: 0;
                transform: rotate(-30deg);
                transform-origin: 0 0;
            }
        }
        .ui-datepicker-next {
            float: right;
            &:before {
                top: -5px;
                right: 0;
                transform: rotate(-30deg);
                transform-origin: 0 100%;
            }
            &:after {
                top: 50%;
                right: 0;
                transform: rotate(30deg);
                transform-origin: 100% 0;
            }
        }
        .ui-datepicker-title {
            font: 400 20px/30px $fira;
            margin: 0 30px;
        }
    }
    .ui-datepicker-calendar {
        width: 100%;
        border-collapse: collapse;
        th {
            font: 500 16px/45px $roboto;
            vertical-align: middle;
            color: $bluemiddle;
            &.ui-datepicker-week-end {
                color: $red;
            }
        }
        td {
            font: 500 18px/45px $roboto;
            vertical-align: middle;
            padding: 3px;
            span, a {
                display: block;
                border-radius: 3px;
            }
            a {
                text-decoration: none;
                color: $bluemiddle;
                background: $white;
                border: 1px solid $blue;
                transition: $transition;
                box-shadow: 0 0 15px rgba(0,0,0,.2);
                &.ui-state-hover {
                    @include gradient(150deg, $bluelight, $blue);
                    color: $white;
                }
            }
            span {
                color: $graylight;
                background: $grayF6;
            }
            &.currentDate {
                a {
                    @include gradient(60deg, $bluelight, $blue);
                    color: $white;
                    box-shadow: 0 0 15px rgba(0,0,0,.2);
                }
            }
            //&.ui-datepicker-current-day {
            //    a, span {
            //        @include gradient(60deg, $bluelight, $blue);
            //        color: $white;
            //        box-shadow: 0 0 15px rgba(0,0,0,.2);
            //    }
            //}
        }
    }
    @include desktop {
        .ui-datepicker-header {
            padding: 13px 20px;
            .ui-datepicker-title {
                font: 400 20px/30px $fira;
            }
        }
        .ui-datepicker-calendar {
            th {
                font: 500 18px/56px $roboto;
            }
            td {
                font: 500 24px/56px $roboto;
            }
        }
    }
}
.centerImg {
    display: block;
    text-align: center;
    margin: 0 0 30px 0;
    img {
        max-width: 100%;
        height: auto;
    }
}
.fixScroll {position: fixed; z-index: 100; top: 0;}
.scrollStop {position: relative;}
.blue {
    color: $blue;
}